.overlay{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 0;
    padding: 10px 20px;
    color: silver;
    background-color: black;
    opacity: 0.8;
    overflow: hidden;
    transition: 0.3s;
   
}

.card:hover .overlay {
    height: 100%;
    
}

.note {
    transform: translate(0, 0);
    margin-top: 10px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color:gold
}

